body {
  background-attachment: fixed;
  background-image: url(/static/media/background.f9540e08.jpeg);
  background-size: cover;
  background-position: 50% 50%;
  background-repeat: no-repeat;
  background-attachment: fixed;
  overflow: hidden;
  margin: 0;
  font-family: 'Nobile', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace; }

.footer {
  bottom: calc(10px + 2vmin);
  width: 100%;
  text-align: center;
  position: absolute; }

h1 {
  color: #f53495;
  text-align: center;
  font-size: 50px;
  font-weight: 900;
  height: 100px;
  line-height: 300%;
  margin: auto; }

h2 {
  color: #FFFFFF;
  text-align: center;
  font-weight: normal;
  height: 80px;
  line-height: 250%;
  margin: auto; }

.portrait {
  display: flex;
  align-items: center;
  justify-content: center;
  border: 3px solid white;
  border-radius: 50%;
  margin: 100px auto;
  width: 225px; }

a {
  text-decoration: none; }

.icon {
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  height: calc(25px + 2vmin);
  opacity: 0.65;
  -webkit-transition: all 0.2s linear;
  transition: all 0.2s linear;
  padding-top: 100px;
  padding-right: 2px; }

.profile {
  align-items: center;
  display: flex;
  justify-content: center;
  flex-direction: column; }

